h1{
    font-size: 2rem;
    text-align: left;
    font-weight: bold;
}
p{
    font-size: 1rem;
    text-align: left;
    padding-top: 1.5vh;
}
.content-wrapper {
    border-radius: 0px;
    width: 45%;
    height: auto;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    top: -8vw;
    transition: width 0.4s ease; /* This is for enlargening the width when @media screen size gets too small for 45%. Mhhhhhh the transition looks soooo smooth ;) */
}
.page-content{
    padding: 20px;
}
.avatar{
    border-radius: 50%;
    width: 150px;
    height: 150px;
    margin-top: 25px;
}

.logo-text{
    
    display: inline-block;

    /*
    display: block;
    margin-left: -10px;
    font-size: 1.6rem;
    */
}

.navbar{
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: sticky;
    padding-top: 2vh;
    padding-bottom: 2vh;
    z-index: 20;
}

.nav-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 45%;
    height: 80px;
    max-width: calc(100% - 100px);
    transition: width 0.4s ease;
}

.nav-logo{
    align-items: center;
    margin-right: 20px;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    flex-grow: 1;
    font-size: 2rem;
}

.nav-menu{
    display: flex;
    list-style: none;
    text-align: center;
}

.nav-links{
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    border-bottom: 3px solid transparent;
}

.nav-item{
    line-height: 40px;
    margin-right: 1rem;
    font-size: 1.5rem;
}
.nav-item:after{
    content: "";
    display: block;
    height: 3px;
    width: 0;
    background: transparent;
    transition: width 0.5s ease, background-color 0.5s ease;
}
.nav-icon{
    display:none;
    font-size: 1.5rem;
}
.fa-code{
    margin-left: 1rem;
}

#tsparticles {
    z-index: 100;
}

.resume{
    width: 90vw;
    height: 120vh;
    border-radius: 0px;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    top: -8vw;
    overflow: hidden;
}

/* Slowly enlarge the widths of the content-wrapper and the navbar  */
@media screen and (max-width: 1530px){
    .content-wrapper{
        width: 60%;
    }
    .nav-container{
        width: 60%;
    }
}

/* Collapse the nav-bar */
@media screen and (max-width: 1140px){
    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 95%;
        border-top: 1pxsolid #fff;
        position: absolute;
        top: 100px;
        left: -110%;
        opacity: 1;
        transition: all 0.4s ease;
        padding-left: 2.5%;
    }

    .nav-menu.active{
        left: 0px;
        opacity: 1;
        transition: all 0.4s ease;
        z-index: 1;
    }

    .nav-icon{
        display: block;
        margin-right: 1rem;
    }
}

/* Enlarge the page-content and turn navbar display to block */
@media screen and (max-width: 714px){
    .content-wrapper{
        width: 85%;
    }
    .nav-container{
        width: 85%;
    }
}
